import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col } from 'antd';
import { TableData } from 'components/common';
import { getStudents } from 'services/AdditionalAdmissionPapersPage';
import { getQueryParams } from 'utils/urlUtil';
import StructCodeForm from './StructCodeForm';
import FormSearch from './FormSearch';

const columns = [
  {
    title: 'M SV',
    dataIndex: 'code',
    key: 'code',
  },
  {
    title: 'H v tn',
    dataIndex: 'fullname',
    key: 'fullname',
  },
  {
    title: 'Ngy sinh',
    dataIndex: 'dob',
    key: 'dob',
  },
  {
    title: 'Gii tnh',
    dataIndex: 'gender',
    key: 'gender',
  },
  {
    title: 'Tn lp',
    dataIndex: 'className',
    key: 'toScores',
  },
];

class SetStudentCodePage extends Component {
  state = {
    reload: 0,
    paramSearchClass: {},
  };

  componentDidMount = () => {
    this.handleChangeQueryParams();
  };

  componentWillReceiveProps = () => {
    this.handleChangeQueryParams();
  };

  handleChangeQueryParams = () => {
    this.setState({ paramSearchClass: getQueryParams() });
  };

  render() {
    const { reload, paramSearchClass } = this.state;
    return (
      <Card title="Lp m sinh vin">
        <Row>
          <Col span={24}>
            <FormSearch />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <h5>Thit lp cu trc m sinh vin</h5>
          </Col>
          <Col span={24}>
            <StructCodeForm />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              formSearch
              service={getStudents}
              paramSearch={paramSearchClass}
              columns={columns}
              allowGetData={!!paramSearchClass.courseId}
              rowKey="id"
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <SetStudentCodePage />
  </Page>
);



