import React, { Component } from 'react';
import { Row, Col, Form, Button, Input, Checkbox } from 'antd';

@Form.create({ name: 'formScholarshipApproval' })
class StructCodeForm extends Component {
  state = {};

  handleReset = () => {
    this.props.form.resetFields();
  };

  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const styleForm = {
      display: 'flex',
      flexWrap: 'wrap',
      alignItems: 'flex-end',
    };
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24} style={styleForm}>
          <Col span={6}>
            <Form.Item label=" di ca s t tng">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Bt u nh t s">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phn u ca m">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Phn cui ca m">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="Gii m sinh vin s l">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="n">
              {getFieldDecorator('email', { initialValue: '' })(<Input />)}
            </Form.Item>
          </Col>
          <Col span={6}>
            <Form.Item label="">
              <Checkbox>Sinh vin cha c m</Checkbox>
            </Form.Item>
          </Col>
          <Col span={6}>
            <Row>
              <Col span={12}>
                <Form.Item label="">
                  <Button type="primary" htmlType="submit">
                    Lp m t ng
                  </Button>
                </Form.Item>
              </Col>
              <Col span={12}>
                <Form.Item label="">
                  <Button type="danger">Xa tt c m</Button>
                </Form.Item>
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    );
  }
}

export default StructCodeForm;



